
:root{
    --color-primary: #191d2b;
    --color-secondary: #27AE60;
    --color-white: #FFFFFF;
    --color-black: #000;
    --color-grey0: #f8f8f8;
    --color-grey-1: #dbe1e8;
    --color-grey-2: #b2becd;
    --color-grey-3: #6c7983;
    --color-grey-4: #454e56;
    --color-grey-5: #2a2e35;
    --color-grey-6: #12181b;
    --br-sm-2: 14px;
    --box-shadow-1: 0 3px 15px rgba(0,0,0.2);
}

.main-div{
    background-color: var(--color-primary);
    color: var(--color-white);
    font-size: 1.1rem;
    font-family: 'Poppins',sans-serif;
    width: 100vw;
    height: 100vh;
}

.option3-links{
    display: inline-block;
    text-decoration: none;
    color: inherit;
    font-family: inherit;
}

.option3-header{
    height: 100vh;
    color: var(--color-white);
    overflow: hidden;
}

.option3-section{
    min-height: 100vh;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: 3rem 18rem;
}
.section{
    transform: translateY(-100%) scale(0);
    transition: all .4s ease-in-out;
    background-color: var(--color-primary);
}
.sec1{
    display: none;
    transform: translateY(0) scale(1);
    background-color: blue;
}
.sec2{
    display: none;
    transform: translateY(0) scale(1);
    background-color: red;
}
.sec3{
    display: none;
    transform: translateY(0) scale(1);
    background-color: yellow;
}
.sec4{
    display: none;
    transform: translateY(0) scale(1);
    background-color: aqua;
}
.sec5{
    display: none;
    transform: translateY(0) scale(1);
}

//Controls
.controlls{
    position: fixed;
    z-index: 10;
    top: 50%;
    right: 3%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    transform: translateY(-50%);
    .active-btn{
        background-color: var(--color-secondary) !important;
        transition: all .4s ease-in-out;
        i{
            color: var(--color-white) !important;
        }
    }
    .control{
        padding: 1rem;
        cursor: pointer;
        background-color: var(--color-grey-4);
        width: 55px;
        height: 55px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: .7rem 0;
        box-shadow: var(--box-shadow-1);
        i{
            font-size: 1.2rem;
            color: var(--color-grey-2);
            pointer-events: none;
        }
    }
}

